.container {
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1360px) {
  .container {
    padding: 0 140px;
  }
}

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  min-width: 320px;
  margin: 0;
  padding: 0;
}

.contrast-text--orange {
  color: #d67e34;
}
.contrast-text--lbrown {
  color: #bc9060;
}

.main-header {
  position: relative;
  background-image: url("../img/mobile/header-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .main-header {
    background-image: url("../img/mobile/header-bg@2x.png");
    background-size: cover;
  }
}

.main-header__intro-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 250px;
  padding-bottom: 304px;
}

.main-header__title {
  font-size: 28px;
  line-height: 34px;
  font-weight: 400;
  color: #d67e34;
  text-align: center;
  margin: 0;
}

.main-header__our-restaurant {
  font-size: 40px;
  line-height: 46px;
}

.main-header__eat-home {
  font-size: 15px;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 5px;
  margin-bottom: 0;
}

.main-header__menu-restaurant {
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d67e34;
  width: 137px;
  height: 47px;
  margin-top: 45px;
}

.main-header__advantages {
  position: absolute;
  bottom: -780px;
  display: flex;
  flex-direction: column;
  row-gap: 14px;
  z-index: 5;
}

.main-header__advantage-block {
  position: relative;
  text-align: center;
  background-color: #ffffff;
  width: 264px;
  height: 156px;
  padding: 100px 13px 20px 13px;
  box-shadow: 10px 10px 40px 20px rgba(0, 0, 0, 0.1);
}
.main-header__advantage-block::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 100px;
  background-image: url("../img/icons/dish.svg");
  background-repeat: no-repeat;
  width: 87px;
  height: 87px;
}

.main-header__advantage-title {
  font-size: 23px;
  line-height: 30px;
  font-weight: 400;
  margin-top: 0;
}

.main-header__advantage-description {
  font-size: 20px;
  line-height: 25px;
  color: #656363;
}

.main-nav {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
}

.main-nav__logo {
  display: inline-block;
  background-image: url("../img/icons/logo.svg");
  background-repeat: no-repeat;
  width: 45px;
  height: 31px;
}

.main-nav__button {
  display: inline-block;
  background-repeat: no-repeat;
  background-color: transparent;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.main-nav__button--open {
  width: 31px;
  height: 18px;
  background-image: url("../img/icons/menu.svg");
}
.main-nav__button--close {
  position: absolute;
  top: 25px;
  right: 20px;
  width: 23px;
  height: 23px;
  background-image: url("../img/icons/menu-close.svg");
}

.main-nav__menu {
  position: absolute;
  top: 0px;
  left: -15px;
  right: -15px;
  height: 95vh;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.95);
  padding: 15px;
  transform: translateY(-100%);
  transition: 0.4s;
  z-index: 10;
}
.main-nav__menu--open {
  transform: translateY(0);
  transition: 0.4s;
}

.main-nav__nav-list {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
  margin: 45px 0 0 0;
  padding: 0;
  list-style: none;
}

.main-nav__nav-link {
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 12vh;
  transition: 0.4s;
}
.main-nav__nav-link:hover {
  color: #656363;
  transition: 0.4s;
}

.main-nav__reservation {
  margin-top: 50px;
}

.main-nav__number {
  text-decoration: none;
  color: #000000;
}
.main-nav__number:hover, .main-nav__number:focus {
  text-decoration: underline;
}

.main-nav__feedback {
  color: #656363;
}

.main-nav__order {
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d67e34;
  width: 157px;
  height: 47px;
  margin-top: 15px;
}

.our-story {
  margin-top: 800px;
}